body {
    margin: 0;
}

.container {
    padding: 10px;
}

p {
    margin: 0;
    padding: 10px;
}

h1,
h2,
h3,
h4 {
    margin: 0;
    padding-bottom: 1rem;
}

a {
    text-decoration: none;
}

button {
    opacity: 0.8;
}

.section-layout {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.song-list {
    color: white;
    background-color: #272343;
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: 5px;
}

i {
    cursor: pointer;
}

.icon-fill {
    color: #FFD803;
}

.icon-lightblue {
    font-size: 20px;
    color: #BAE8E8;
}

.total-price {
    text-align: right;
    font-size: 28px;
}
.total {
    color: #BAE8E8;
}

.artist {
    font-size: 18px;
}
.price {
    font-size: 20px;
    color: #FFD803;
}

.buybtn {
    background-color: #FFD803;
    padding: 10px 20px;
    border: none;
}

.buybtn:hover {
    outline: none;
}



@media(min-width: 650px) {
    .container {
        max-width: 750px;
        margin: auto;
    }
}

.add-form {
    
}

fieldset {
    border: none;
    max-width: 80%;
}

.add-field {
    width: 100%;
    background-color: #BAE8E8;
    border-radius: 5px;
    border: none;
    height: 3rem;
    padding: 0 10px;
}

input,
button,
textarea {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
}

textarea {
    width: 100%;
    background-color: #BAE8E8;
    border-radius: 5px;
    border: none;
}

.addBtn {
    background-color: rgb(178, 190, 7);
    color: white;
    border-radius: 5px;
    border: none;
    padding: 10px 20px;
}

.addBtn:hover {
    outline: none;
}